:root {
  --colors-primary: #2399ef;
  --colors-primary-rgb: 35, 153, 239;
}

html, body, #root {
  height: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #f3f4f9;
  color: #121f43;
  overflow-x: hidden;
}

body * {
  box-sizing: border-box;
  margin: 0;
}

.flex {
  display: flex;
}

.flex-centered {
  display: flex;
  align-items: center;
  justify-content: center;
}

a {
  text-decoration: none;
}

a, a:visited {
  color: var(--colors-primary);
}

a.MuiListItem-root {
  color: initial;
}

h2, h4, h6 {
  font-weight: 600;
}

h5, h6 {
  font-size: 0.9rem;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  background: white;
}

::-webkit-scrollbar-thumb {
  background: #8da2b799;
  border-radius: 50px;
}

::-webkit-scrollbar-thumb:hover {
  background: #8da2b799;
}

.MuiChip-root.Complete {
  background-color: #27ce881a;
  color: #27ce88;
}

.MuiChip-root.New {
  background-color: #0040ff17;
  color: #273bce;
}

.MuiChip-root.Next.Up {
  background-color: #8527ce1a;
  color: #8527ce;
}

.MuiChip-root.In.Progress {
  background-color: #ffa5001c;
  color: orange;
}

.MuiChip-root.Currently.Writing {
  background-color: #ffc0cb2b;
  color: pink;
}

.MuiChip-root.Pending.Approval {
  background-color: #ff00000f;
  color: red;
}

.MuiChip-root.Approved {
  background-color: #00d0d01c;
  color: #00d0d0;
}

.MuiChip-root.Ready.to.Implement {
  background-color: #a52a2a12;
  color: brown;
}

[hidden] {
  display: none !important;
}